import React, { useEffect, useState } from 'react'
import Session from './Login'
import io from 'socket.io-client'
import audio from "../audio/audio.mp3"


const socket = io('https://chat-app-serverside.herokuapp.com/')
function Home(_user) {
    let _audio = new Audio(audio);
    let _name = _user.user.displayName;
    let _dp = _user.user.photoURL;
    function formatAMPM(date) {
        var hours = date.getHours();
        var minutes = date.getMinutes();
        var ampm = hours >= 12 ? 'pm' : 'am';
        hours = hours % 12;
        hours = hours ? hours : 12; // the hour '0' should be '12'
        minutes = minutes < 10 ? '0' + minutes : minutes;
        var strTime = hours + ':' + minutes + ' ' + ampm;
        return strTime;
    }
    const appendJoin = (message) => {
        let chatBox = document.getElementById('chat-box');
        const messageElement = document.createElement('div');
        messageElement.className = 'sender-bubble-wrapper';
        messageElement.innerHTML = `<div class="sender message-bubble"><div style="font-style:italic;" class="bubble-text">` + message + `</div><div class="bubble-time">` + formatAMPM(new Date()) + `</div></div></div>`
        chatBox.append(messageElement)
    }
    const sentMessage = (message) => {
        let chatBox = document.getElementById('chat-box');
        const messageElement = document.createElement('div');
        messageElement.className = 'sender-bubble-wrapper';
        messageElement.innerHTML = `<div class="sender message-bubble"><div class="bubble-name">You</div><div class="bubble-text">` + message + `</div><div class="bubble-time">` + formatAMPM(new Date()) + `</div></div><img class="dp" src=` + _dp + ` alt="dp">`
        chatBox.append(messageElement)
        scrollDown();
    }
    const [mute, setMute] = useState(false);

    const recieveMessage = (name, message, dp) => {
        _audio.play();
        let chatBox = document.getElementById('chat-box');
        const messageElement = document.createElement('div');
        messageElement.className = 'reciever-bubble-wrapper';
        messageElement.innerHTML = `<img class="dp" src=` + dp + ` alt="dp"><div class="message-bubble">
        <div class="bubble-name">`+ name + `</div>
        <div class="bubble-text">`+ message + `</div>
        <div class="bubble-time">`+ formatAMPM(new Date())
            + `</div>
        </div>`
        chatBox.append(messageElement)
        scrollDown();

    }
    const scrollDown = () => {
        document.getElementById('chat-box').childNodes[document.getElementById('chat-box').childNodes.length - 1].scrollIntoView({ behavior: 'smooth' })
        if (document.getElementById('chat-box').scrollTop / document.getElementById('chat-box').scrollHeight <= 0.70)
            document.getElementById('scrollDown').style.display = 'none';
        else
            document.getElementById('scrollDown').style.display = 'block'
    }
    const enterKey = (e) => {
        // if (e.key === "Enter")
        //     document.getElementById('message-send-button').click()
    }
    const [message, setMessage] = useState('');
    const sendMessage = () => {
        if(message!==''){
            let data = {
                name: _name,
                picture: _dp,
                message: message
            }
            socket.emit('send-message', (data));
            socket.emit('typing', { message: '', name: _name })
            sentMessage(message)
            setMessage("");
            document.getElementById('message').blur();
        }
    }
    useEffect(() => {
        socket.emit('new-user-joined', _name)
        socket.on('user-joined', (name) => {
            appendJoin(name + ' Joined the Chat.')
        })
    }, [])
    useEffect(() => {
        socket.on('recieve-typing', (data) => {
            let typers = '';
            for (let i = 0; i < Object.keys(data).length; i += 2) {
                if (Object.keys(data)[i] !== _name)
                    typers += (Object.keys(data)[i])
                if (Object.keys(data).length - 2 !== i && typers !== '')
                    typers += ' + ';
            }
            if (typers !== '')
                typers = (typers + ' Typing...')
            document.getElementById('typing').innerHTML = typers;
        });

        socket.on('recieve-message', (data) => {
            document.getElementById('chat-box').addEventListener('DOMNodeInserted', () => {
                document.getElementById('scrollDown').style.display = 'block'
            })
            recieveMessage(data.name, data.message, data.picture)
        });

    }, [])
    const onTyping = (message) => {
        setMessage(message);
        socket.emit('typing', { message: message, name: _name })
    }
    return (
        <div className="home-container">
            <div className="main-chat-container">
                <div className="chat-header">
                    <div className="m-10">
                        <div className="header-text">Realtime Chat Around the World</div>
                    </div>
                    <div className="m-10">
                        <div onClick={() => setMute(!mute)}>{(mute) ? (<i className="fas fa-volume-mute"></i>) : (<i className="fas fa-volume-up"></i>)}</div>
                        <Session.SignOut />
                    </div>
                </div>
                <div id="chat-box" className="chat-box">

                </div>
                <button onClick={(() => scrollDown())} id="scrollDown"><i className="fas fa-arrow-alt-circle-down"></i></button>
                <div id="sending-component">
                    <div id="typing"></div>
                    <form onSubmit={e => e.preventDefault()} id="send-message-wrapper" className="send-message-wrapper">
                        <input type="text" autoComplete="off" onKeyUp={(e) => enterKey(e)} name="message" id="message" onChange={e => onTyping(e.target.value)} value={message} placeholder="Enter Your Message" />
                        <button id="message-send-button" onClick={() => sendMessage()}>Send</button>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Home;
